* {
	font: 14px 'Century Gothic', Futura, sans-serif;
	font-size: 20px;
}

body {
	padding: 0;
	margin: 0;
	overflow: hidden;
	background-color: #4158d0;
	background-image: linear-gradient(
		43deg,
		#6493d1 0%,
		#70ffff 20%,
		#c850c0 46%,
		#4158d0 67%,
		#c850c0 100%
	);
	background-size: 300% 300%;
	display: flex;
	justify-content: center;
	align-items: center;
	height: 100vh;
	animation: bg 25s ease-in infinite alternate-reverse;
}

@keyframes bg {
	0%,
	100% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100%, 50%;
	}
}

.container {
	top: 50%;
	left: 50%;
	position: absolute;
	transform: translate(-50%, -50%);
	margin: 0 auto;
	text-align: center;
	padding: 10px;
	background: rgba(255, 255, 255, 0.1);
	border-radius: 6px;
	backdrop-filter: blur(15px);
	border-top: 1px solid rgba(255, 255, 255, 0.2);
	border-left: 1px solid rgba(255, 255, 255, 0.2);
	box-shadow: 5px 5px 30px rgba(0, 0, 0, 0.2);
}

.keypad {
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	grid-auto-rows: minmax(60px, auto);
}

input[type='text'] {
	height: 45px;
	width: 249px;
	padding: 0 5px;
	margin-bottom: 10px;
	background: rgba(255, 255, 255, 0.05);
	border-radius: 6px;
	backdrop-filter: blur(15px);
	border: none;
	border-top: 1px solid rgba(255, 255, 255, 0.2);
	border-left: 1px solid rgba(255, 255, 255, 0.2);
	box-shadow: 5px 5px 30px rgba(0, 0, 0, 0.2);
	color: #fff;
	text-align: right;
	font-size: 25px;
	font-weight: 500;
	letter-spacing: 1px;
}

#clear {
	grid-column: 1/3;
	grid-row: 1;
}

#result {
	grid-column: 3/5;
	grid-row: 5;
}

button {
	margin: 5px;
	cursor: pointer;
	background: rgba(255, 255, 255, 0.05);
	border-radius: 6px;
	backdrop-filter: blur(15px);
	border: none;
	border-top: 1px solid rgba(255, 255, 255, 0.2);
	border-left: 1px solid rgba(255, 255, 255, 0.2);
	box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.2);
	color: #fff;
	font-weight: 500;
	user-select: none;
}

input:focus,
input:active,
button:focus,
button:active {
	outline: none;
}

button:active {
	box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.2);
	transform: translate(2px, 2px);
}

.highlight {
	background: rgba(86, 203, 219, 0.5);
	font-size: 25px;
}

@media screen and (max-width: 768px) {
	button {
		cursor: default;
	}
}
